/*   ,      */
.group {
	&:before,
	&:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}
}

/*   */
.groupIB {
	box-sizing: border-box;
	display: inline-block;
	width: 100%;
}

.hideAway {
	left: -9999px;
	position: absolute;
	top: -9999px;
	visibility: hidden;
}
.hideAway\! {
	left: -9999px !important;
	position: absolute !important;
	top: -9999px !important;
	visibility: hidden !important;
}

.hideAway--clear {
	left: auto;
	position: static;
	top: auto;
	visibility: visible;
}
.hideAway--clear\! {
	left: auto !important;
	position: static !important;
	top: auto !important;
	visibility: visible !important;
}

.hideSr {
	border: 0;
	clip: rect(0,0,0,0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}


