.fillLine {
	display: inline-block;
	overflow: hidden;
	-ms-text-overflow: ellipsis;
			text-overflow: ellipsis;
	white-space: nowrap;
	max-width: 100%;
}


/*
  

http://www.mobify.com/blog/multiline-ellipsis-in-pure-css/

```html
<p class="Ellipsify"><span class="Ellipsify__text"> .  . ,     ,       . ,       ,       .<span></p>
```
*/

.Ellipsify {
	height: calc($lineHeight * 4);
	line-height: $lineHeight;
	overflow: hidden;
	
	&:before {
		content: "";
		float: left;
		height: calc($lineHeight * 4);
		width: 5px;
	}
	&:after {
		box-sizing: content-box;
		content: "\02026";  
		float: right;
		left: 100%;
		margin-left: -1em;
		padding-right: 5px;
		position: relative;
		text-align: right;
		top: -$lineHeight;
		width: 1em;
	}
}
.Ellipsify__text {
	float: right;
	margin-left: -5px;
	width: 100%;
}


/*    */
.caret {
	border: .36em solid transparent;
	border-top-color: currentColor;
	border-bottom-width: 0;
	display: inline-block;
	height: 0;
	margin-left: 2px;
	vertical-align: middle;
	width: 0;
	
	.Btn & {
		margin-left: 0;
	}
}
.caret--top {
	border-top-color: transparent;
	border-top-width: 0;
	border-bottom-color: currentColor;
	border-bottom-width: .36em;
}


/*   ,      */
.larr,
.rarr {
	position: relative;
}
.larr {
	margin-left: 1.5em;
}
.rarr {
	margin-right: 1.5em;
}
.larr:before,
.rarr:after {
	font-size: 1.2em;
	line-height: 1em;
	position: absolute;
	top: 0;
}
.larr:before {
	content: "\2190";
	margin-right: .3em;
	right: 100%;
}
.rarr:after {
	content: "\2192";
	left: 100%;
	margin-left: .3em;
}

.Delta {
	color: #999;

	&--positive {
		color: #48b28b;

		&:after {
			content: '\2191';
			position: relative;
			top: -.15em;
		}
	}

	&--negative {
		color: #e45959;

		&:after {
			content: '\2193';
			position: relative;
			top: -.15em;
		}
	}
}

