/*!

	BASE
		BASE - ROOT
		BASE - HTML5
		BASE - EMBEDDED
		BASE - FORMS
		BASE - TABLES
		BASE - LINKS
		BASE - LISTS
		BASE - TYPOGRAPHY
 
 */


$selectionBG: #b3d3fd !default;
$selectionC: #000 !default;

$lineHeight: 1.5em !default;



/* BASE
---------------------------------------------------------------------*/

/* BASE - ROOT
---------------------------------------------------------------------*/

html {
	font-size: 62.5%;
	font-family: sans-serif;
	overflow-y: scroll; /*   -    */
	-webkit-font-smoothing: antialiased; /*      */
	
	/* http://stackoverflow.com/questions/6210788/how-to-avoid-ios-automatic-font-size-adjustment        */
	-webkit-text-size-adjust: 100%;
	    -ms-text-size-adjust: 100%;
}

body {
	line-height: strip($lineHeight);
	margin: 0; /*    */
}

/*  :        */
::-moz-selection {
	background-color: $selectionBG;
	color: $selectionC;
	text-shadow: none;
}
::selection {
	background-color: $selectionBG;
	color: $selectionC;
	text-shadow: none;
}

[hidden], /*   789 */
template { /*   ,   22 */
	display: none;
}



/* BASE - HTML5
---------------------------------------------------------------------*/

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
	display: block; /*    891011   */
}

audio,
canvas,
progress,
video {
	display: inline-block; /*    89 */
	vertical-align: baseline; /*   ,    */
}

audio:not([controls]) {
	display: none; /*      */
	height: 0; /*      5 */
}



/* BASE - EMBEDDED
---------------------------------------------------------------------*/

img {
	border: 0; /*  ,      89 */
	vertical-align: middle; /*    */

	.ielte7 & {
		-ms-interpolation-mode: bicubic; /*      */
	}
}

svg:not(:root) {
	overflow: hidden; /*   91011 */
}

figure {
	margin: 0; /*  789,  5,  11 */
}



/* BASE - FORMS
---------------------------------------------------------------------*/

form {
	.ielte7 & {
		margin: 0;
	}
}

fieldset {
	border: 0;
	margin: 0;
	padding: 0;
	
	/* http://stackoverflow.com/questions/17408815/fieldset-resizes-wrong-appears-to-have-unremovable-min-width-min-content/17863685#17863685 */
	min-width: 0;
}

@-moz-document url-prefix() {
	fieldset {
		display: table-cell;
		vertical-align: middle;
	}
}

legend {
	border: 0;
	padding: 0;
	white-space: normal;

	.ielte7 & {
		margin-left: -7px;
	}
}

label,
input[type="checkbox"],
input[type="radio"],
button,
select {
	cursor: pointer;
}
html input[disabled],
button[disabled],
select[disabled] {
	cursor: default;
}

button,
input,
optgroup,
select,
textarea {
	box-sizing: border-box;
	color: inherit;
	font: inherit;
	margin: 0;
	vertical-align: baseline;

	.ielte7 & {
		vertical-align: middle;
	}
}

button,
input {
	line-height: normal;
}

button,
select {
	text-transform: none;
}

button {
	overflow: visible; /*   `hidden`   */
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button; /*      */
	cursor: pointer;

	.ielte7 & {
		overflow: visible; /*   */
	}
}

button[disabled],
html input[disabled] {
	cursor: default;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box; /*     */
	padding: 0;
	
	/*     */
	margin-top: -.17em;
	vertical-align: middle;

	.ielte7 & {
		height: 13px;
		width: 13px;
	}
}

/*       */
input[type="search"] {
	-webkit-appearance: textfield;
	box-sizing: content-box;
}

/*        */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto; /*      */
}

/*     */
button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

/*       */
select:focus,
button:focus,
textarea:focus,
input:focus,
[tabindex]:focus {
	outline: 0;
}

/*      */
textarea {
	overflow: auto;
	resize: vertical;
	vertical-align: top;
}

legend {
	border: 0; /*       */
	padding: 0;
}

optgroup {
	font-weight: 700; /*     */
}



/* BASE - TABLES
---------------------------------------------------------------------*/

/*     */
table {
	border-collapse: collapse;
	border-spacing: 0;
}

td, th {
	padding: 0;
}



/* BASE - LINKS
---------------------------------------------------------------------*/

a:focus {
	outline: thin dotted; /*     */
}

a:active,
a:hover {
	outline: 0; /*      */
}



/* BASE - LISTS
---------------------------------------------------------------------*/

li ul,
li ol {
	margin: calc($lineHeight / 3) 0 calc($lineHeight / 3 * 2);
}
ul, ol, dl {
	margin: 0 0 $lineHeight;
}
ul, ol {
	padding: 0 0 0 1.5em;
}
li, dt, dd {
	margin-top: calc($lineHeight / 3);
	padding-left: 0;
	position: relative;
}
li:first-child,
dt:first-child,
dd:first-child {
	margin-top: 0;
}
dt {
	font-weight: bold;
}
dd {
	margin-left: 3em;
}



/* BASE - TYPOGRAPHY
---------------------------------------------------------------------*/

h1, h2, h3, h4, h5, h6 {
	font-weight: bold;
}

h1 {
	font-size: 2em;
	line-height: 1em;
	margin: calc($lineHeight / 2em) 0 calc($lineHeight / 2em / 2);
}

h2 {
	font-size: 1.5em;
	line-height: calc($lineHeight / 1.5em);
	margin: calc($lineHeight / 1.5em) 0 calc($lineHeight / 1.5em / 2);
}

h3 {
	font-size: 1.17em;
	line-height: calc($lineHeight / 1.17em);
	margin: calc($lineHeight / 1.17em) 0 calc($lineHeight / 1.17em / 2);
}

h4 {
	font-size: 1em;
	line-height: calc($lineHeight / 1em);
	margin: calc($lineHeight / 1em) 0 calc($lineHeight / 1em / 2);
}

h5 {
	font-size: .83em;
	line-height: calc($lineHeight / .83em);
	margin: calc($lineHeight / .83em) 0 calc($lineHeight / .83em / 2);
}

h6 {
	font-size: .67em;
	line-height: calc($lineHeight / .67em);
	margin: calc($lineHeight / .67em) 0 calc($lineHeight / .67em / 2);
}

p,
pre,
blockquote {
	font-size: 1em;
	line-height: $lineHeight;
	margin: 0 0 $lineHeight;
}

p + ul,
p + ol,
p + dl {
	margin-top: calc(-$lineHeight / 2);
}


abbr[title] {
	border-bottom: 1px dotted; /*     89,  5    */
	cursor: help;
}

b, strong {
	font-weight: 700;
}

i, em {
	font-style: italic;
}

dfn {
	font-style: italic; /*     5   */
}

/*      */
hr {
	-moz-box-sizing: content-box;
	     box-sizing: content-box;
	height: 0;
}

/*     789 */
mark {
	background: #ff0;
	color: #000;
}

/*     5   */
code, kbd, pre, samp {
	font-family: monospace, sans-serif;
	font-size: 1em;
}

pre {
	overflow: auto; /*     */
	white-space: pre-wrap; /*      */
}

/*       */
q:lang(en),
.q.en {
	quotes: "\201C" "\201D" "\2018" "\2019";
}
q:lang(ru),
.q.ru {
	quotes: '\00AB' '\00BB' '\201E' '\201C';
}

big {
	font-size: 120%;
}
small {
	font-size: 80%;
}

/*       */
sub, sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}
sup {
	top: -.5em;
}
sub {
	bottom: -.25em;
}

wbr {
	display: inline-block;
}
