
$lineHeight: 1.5em !default;

$infoColor: #666 !default;
$errorColor: #c00 !default;
$successColor: #693 !default;


/* HELPERS
---------------------------------------------------------------------*/

/*     */
.d-n { display: none; }
.d-i { display: inline; }
.d-b { display: block; }
.d-i-b { display: inline-block; }

.d-n\! { display: none !important; }
.d-i\! { display: inline !important; }
.d-b\! { display: block !important; }
.d-i-b\! { display: inline-block !important; }

.v-h { visibility: hidden; }
.v-v { visibility: visible; }

.op-0 { opacity: 0; }
.op-1 { opacity: 1; }

.ov-h { overflow: hidden; }
.ov-v { overflow: visible; }



/* ,       */
.m-t-0 { margin-top: 0; }
.m-t-025 { margin-top: calc($lineHeight * .25); }
.m-t-05 { margin-top: calc($lineHeight * .5); }
.m-t-1 { margin-top: $lineHeight; }
.m-t-15 { margin-top: calc($lineHeight * 1.5); }
.m-t-2 { margin-top: calc($lineHeight * 2); }

.m-r-0 { margin-right: 0; }
.m-r-space { margin-right: .4em; }
.m-r-word-s { margin-right: .8em; }
.m-r-word-m { margin-right: 1.2em; }
.m-r-word-l { margin-right: 2em; }

.m-b-0 { margin-bottom: 0; }
.m-b-025 { margin-bottom: calc($lineHeight * .25); }
.m-b-05 { margin-bottom: calc($lineHeight * .5); }
.m-b-1 { margin-bottom: $lineHeight; }
.m-b-15 { margin-bottom: calc($lineHeight * 1.5); }
.m-b-2 { margin-bottom: calc($lineHeight * 2); }

.m-l-0 { margin-left: 0; }
.m-l-space { margin-left: .4em; }
.m-l-word-s { margin-left: .8em; }
.m-l-word-m { margin-left: 1.2em; }
.m-l-word-l { margin-left: 2em; }


.fl-r { float: right; }
.fl-l { float: left; }
.fl-n { float: none; }


.cl-b {	clear: both; }


.w-full,
.w-100p { width: 100%; }
.w-75p { width: 75%; }
.w-50p { width: 50%; }
.w-33p { width: 33.3333%; }
.w-25p { width: 25%; }
.w-20p { width: 20%; }
.w-17p { width: 16.6667%; }
.w-0 { width: 0; }


.v-a-t { vertical-align: top; }
.v-a-m { vertical-align: middle; }
.v-a-b { vertical-align: bottom; }


/*
http://caniuse.com/multicolumn
https://developer.mozilla.org/en-US/docs/Web/Guide/CSS/Using_multi-column_layouts
http://stackoverflow.com/questions/6424088/css-column-breaks
*/

.col-2 { columns: 2; }
.col-3 { columns: 3; }

/*
  http://dev.w3.org/csswg/css-break/
      ,  - 
*/

.breakBefore {
	-webkit-column-break-before: always;
	          page-break-before: always; /*    ,   */
	               break-before: always; /* 10+,  11.112.1 */
}
.noBreakInside {
	-webkit-column-break-inside: avoid;
	          page-break-inside: avoid; /*  */
	               break-inside: avoid; /* 10+,  11.112.1 */
}


.c-info, .c-gray { color: $infoColor; }
.c-error, .c-red { color: $errorColor; }
.c-success, .c-green { color: $successColor; }

.t-a-l { text-align: left; }
.t-a-c { text-align: center; }
.t-a-r { text-align: right; }

.t-d-n { text-decoration: none; }

.w-s-nw { white-space: nowrap; }
