/*doc
```html
<span class="Badge BadgeOutline BadgeStatus wait">
	<span class="BadgeStatus__indicator"></span>
	 / 
</span>
```
*/

.BadgeStatus {
	border-radius: 1.5em;
	color: #666;
	font-size: 10px;
	font-weight: normal;
	line-height: 1.5;
	margin-top: -.2em;
	padding: 1px 6px;
}

@define-mixin BadgeIndicator $bg {
	background-color: $bg;
	border-radius: 50%;
	display: inline-block;
	height: .95em;
	vertical-align: middle;
	margin-top: -.18em;
	width: .95em;
}

.BadgeStatus__indicator {
	@mixin BadgeIndicator #aaa;
}

.BadgeStatus.empty .BadgeStatus__indicator,
.BadgeStatus__indicator.empty {
	box-sizing: border-box;
	border: 2px solid;
	background-color: #fff;
}
.BadgeStatus.wait.empty .BadgeStatus__indicator,
.BadgeStatus__indicator.wait.empty {
	border-color: $attention;
	background-color: #fff;
}
.BadgeStatus.work.empty .BadgeStatus__indicator,
.BadgeStatus__indicator.work.empty {
	border-color: $main;
	background-color: #fff;
}
.BadgeStatus.defect.empty .BadgeStatus__indicator,
.BadgeStatus__indicator.defect.empty {
	border-color: $error;
	background-color: #fff;
}
    
.BadgeStatus.wait {
	border-color: $attention;
	color: $attentionColor;
}
.BadgeStatus.wait .BadgeStatus__indicator,
.BadgeStatus__indicator.wait {
	background-color: $attention;
}

.BadgeStatus.main,
.BadgeStatus.work {
	border-color: $main;
	color: $mainColor;
}

.BadgeStatus.main .BadgeStatus__indicator,
.BadgeStatus__indicator.main,
.BadgeStatus.work .BadgeStatus__indicator,
.BadgeStatus__indicator.work {
	background-color: $main;
}

.BadgeStatus.defect .BadgeStatus__indicator,
.BadgeStatus__indicator.defect {
	background-color: $error;
}

.BadgeStatus.disable {
	border-color: $disable;
	color: $disableColor;
}
.BadgeStatus.disable .BadgeStatus__indicator,
.BadgeStatus__indicator.disable {
	background-color: $disable;
}

.BadgeStatus.apply {
	border-color: $main;
	color: $mainColor;
}

.BadgeStatus.apply .BadgeStatus__indicator,
.BadgeStatus__indicator.apply {
	background: resolve('icons/review_apply.png');
	border-radius: 0;
	width: width('icons/review_apply.png');
	height: height('icons/review_apply.png');
}



/* Toggleable buttons
------------------------------------------------------------------------------*/

/*
<div class="btnMain btnOutline Toggleable Toggleable--on">
	<span class="Toggleable__mark"></span>
	
</div>
*/

.Toggleable {
	white-space: nowrap;
	
	&__mark {
		content: "";
		border: 2px solid;
		border-radius: 50%;
		display: inline-block;
		height: 5px;
		width: 5px;
		margin-right: 4px;
		margin-left: -6px;
		
		.Bubble__trigger & {
			margin-left: 4px;
		}
	}

	& + .BubbleSelect__list {
		padding-left: 25px;
	}
}

.Toggleable--on {
	&,
	&.btnMain,
	&.Bubble__trigger.Bubble__trigger {
		background-color: $main;
		color: #fff;
	}
	.Toggleable__mark {
		background-color: #fff;
	}

	&:hover {
		opacity: .75;
	}

	.BubbleSelect.active & {
		& {
			color: $main;
			opacity: 1;
			
			.Toggleable__mark {
				background-color: currentColor;
				opacity: .75;
			}
		}
	}
}



/* Specs
------------------------------------------------------------------------------*/

/*
<ul class="Specs">
	<li class="Specs__item">
		<h4 class="Specs__header"> <span class="Specs__info">()</span></h4>
		<div class="Specs__info"> </div>
		<div class="Specs__content">   FIDO</div>
	</li>
</ul>
*/

.Specs,
.Specs__item,
.Specs__divider {
	display: block;
	margin-top: 0;
	margin-bottom: 0;
}
.Specs {
	padding-left: 0;
	
	&--set {
		.Specs__item {
			display: inline-block;
			vertical-align: top;
			padding-top: 0;
			padding-bottom: 1.5em;
		}
	}
}
.Specs__item--keyValue {
	.Box {
		padding-y: .25em;
		
		&--value {
			font-weight: bold;
			text-align: right;
		}
	}

	.Box + .Box {
		padding-left: 1em;
	}
}
.Specs__item + .Specs__item {
	padding-top: 1.5em;
}
.Specs__header {
	font-size: 14px;
	font-weight: bold;
	margin-y: 0 .5em;
	
	&--simple, /*  */
	&--mb0 {
		margin-bottom: 0;
	}
}
.Specs__info {
	color: $info;
	margin-bottom: .75em;
	
	.Specs__header + & {
		margin-top: -.25em;
	}

	.Specs__header & {
		font-weight: normal;
	}
}

.Specs__content {
	margin-top: .75em;
}

.Specs__group {
	display: table;
	width: 100%;
}

.Specs__box {
	display: table-cell;
	vertical-align: top;
	
	& + & {
		padding-left: 20px;
	}
}

.Specs__input--thin {
	width: 240px;
}
.Specs__input--fill {
	width: 100%;
}
.Specs__input.Specs__input--error {
	&,
	&:hover {
		border-color: $error;
	}
}

.Specs--divided .Specs__item {
	padding-bottom: 1.5em;
}
.Specs--divided .Specs__item + .Specs__item,
.Specs__divider {
	border-top: 1px solid #dcdcdc;
}
.Specs__divider {
	margin-y: 1.5em;
	
	.contentWrapper > &, /*     */
	.FancyModal__content &,
	&--indent {
		margin-x: -$indentX;
	}
}



/* Upload
------------------------------------------------------------------------------*/

/*
<div class="Upload">
	<ul class="Upload__list">
		<li class="Upload__file">
			<span class="Upload__name">banner-345x120.jpg</span>
			<span class="Upload__remove"></span>
		</li>
	</ul>
	<label class="Upload__label">
		<input type="file" name="" id="" class="Upload__field">
		<span tabindex="0" class="Upload__btn"> </span>
	</label>
</div>
*/

.Upload {
	&__list,
	&__file {
		display: block;
		margin: 0;
	}
	&__list {
		padding-left: 0;
		line-height: 0;
		word-spacing: 15px;
		user-select: none;
	}
	&__name {
		color: $info;
		font-size: 13px;
		margin-right: .5em;
	}
	&__remove {
		@mixin pseudoLink;
		font-size: 11px;
	}

	&__btn {
		@mixin btnOutlineMain;
	}

	&__field {
		display: none;
	}

	&--attachments {
		.Upload__label {
			display: inline-block;
			margin-top: 12px;
		}
	}
}



/* IMAGE UPLOADER
------------------------------------------------------------------------------*/
/* <div class="ImageUploader">
		<img class="ImageUploader__preview" alt="">
		<label class="ImageUploader__label">
			<input class="ImageUploader__field" type="file">
		</label>
</div> */

.ImageUploader {
	border: 1px solid #ddd;
	background-color:#fff;
	cursor: pointer;
	position: relative;
	width: 78px;
	height: 78px;

	&__field {
		display: none;
	}

	&__label {
		background: resolve('icons/upload_image.png') no-repeat 55% 50%;
		display: block;
		height: 100%;
		width: 100%;
		transition: background-color .15s ease;
		position: relative;
		z-index: 1;
	}

	&__preview {
		width: 100%;
		height: auto;
		transition: z-index .15s ease;
		position: absolute;
		z-index: 2;
	}

	&:hover {
		.ImageUploader__preview {
			z-index: 0;
		}

		.ImageUploader__label {
			background-color: rgba(130, 130, 130, .6);
		}
	}
}



