.Tab__wrapper {
	border-bottom: 1px solid #dadada;
	width: 100%;
}

.Tab__list {
	margin-left: $indentX;
	margin-bottom: -1px;
	padding-left: 0;
}

.Tab {
	border: 1px solid transparent;
	cursor: pointer;
	display: inline-block;
	margin-top: 0;
	padding: .55em .75em;
	vertical-align: top;
	user-select: none;
}
.Tab--active {
	background-color: #fff;
	border-color: #dadada;
	border-bottom-color: #fff;
	color: #000;
	cursor: default;
	font-weight: bold;
	pointer-events: none;
}

.Tab__text {
	color: $link;
	font-weight: bold;
}
.Tab--active .Tab__text {
	border-bottom: 0;
	color: #000;
	cursor: default;
}
.Tab__info {
