/* RATING
------------------------------------------------------------------------------*/

.Rating {
	&:before {
		content: "";
		display: inline-block;
		background-image: resolve('icons/rating-no.png');
		background-repeat:no-repeat;
		background-size: contain;
		vertical-align: middle;
		width: width('icons/rating-no.png');
		height: height('icons/rating-no.png');
	}

	&--bad:before {
		background-image: resolve('icons/rating-bad.png');
	}

	&--good:before {
		background-image: resolve('icons/rating-good.png');
	}
}



/* FILE
------------------------------------------------------------------------------*/

.File {
	overflow: hidden;
	display: inline-block;
	position: relative;
	word-spacing: normal;
	padding-left: 34px;
	height: 30px;
	margin: 0;
	margin-top: 12px;
	cursor: default;
	line-height: 1.3;

	&__name {
		display: block;
		font-size: 13px;
		color: #006cb5;
		transition: color .2s;
	}

	&__size,
	&__hint {
		display: block;
		color: #666;
		font-size: 11px;
		user-select: none;
	}

	&:hover {
		cursor: pointer;
	}

	&:hover &__name {
		color: #28bd8b;	
	}

	&:before,
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 25px;
		height: 30px;
		background-image: resolve('icons/file-types.png');
		background-repeat: no-repeat;
	}

	&--zip:before {
		background-position: -30px 0;
	}
	&--pdf:before {
		background-position: -60px 0;
	}
	&--doc:before {
		background-position: -90px 0;
	}
	&--xls:before {
		background-position: -120px 0;
	}
	&--jpg:before {
		background-position: -150px 0;
	}
	&--png:before {
		background-position: -180px 0;
	}

	&:after {
		background-position: -210px 0;
		opacity: 0;
	}	
}

.File--upload {
	.File__name {
		color: #000;
	}
	
	&:before,
	&:after {
		transition: opacity .2s;
	}

	&:hover {
		.File__name {
			color: #e45959;
		}
		.File__size {
			height: 0;
			overflow: hidden;
		}
		&:before {
			opacity: 0;
		}
		&:after {
			opacity: 1;
		}
	}
}



/* RESPONSIBLE LIST
------------------------------------------------------------------------------*/

.Responsible {
	font-size: 13px;
	max-height: 300px;
	width: 200px;
	margin: -4px -8px;
	padding: 5px 0;
	overflow-y: auto;
	
	&__item {
		padding: 5px 8px;
		
