		&--disable {
			background-color: #e9e9e9;
			color: #666;
			cursor: default;
			pointer-events: none;
		}
	}
}



/* TIPTIP CLIENT-INFO
------------------------------------------------------------------------------*/

.clientInfo {
	font-size: 13px;
	padding: 6px 4px;
	
	&__box + &__box {
		margin-top: 8px;
	}

	&__value {
		font-weight: bold;
	}
}



/* UNREAD COMMENTS LIST
------------------------------------------------------------------------------*/

.UnreadComments {
	box-sizing: border-box;
	max-width: 360px;
}

.UnreadComment {
	border-top: 1px solid #dadada;
	display: table;
	margin: 0 -8px;
	
	&:first-child {
		border-top: none;
	}
	
	&__box {
		display: table-cell;
		vertical-align: top;
		padding: 14px 0;

		&--indicator {
			padding-left: 16px;
			width: 17px;
		}

		&--mainInfo {
			padding-right: 16px;
		}
	}
	
	&__item + &__item {
		margin-top: 5px;
	}

	&__item {
		&--name-date {
			color: #999;
		}
	}
}



/* SCORE
------------------------------------------------------------------------------*/

.Score {
	display: inline-block;
	background-image: resolve('icons/gray-star.png');
	background-repeat: repeat-x;
	width: calc((width('icons/gray-star.png') * 5) - 2);
	height: height('icons/gray-star.png');
	vertical-align: top;
	margin-top: .1em;
	
	&__fill {
		display: block;
		background-image: resolve('icons/green-star.png');
		height: 100%;
		
		.Score--orange & {
			background-image: resolve('icons/orange-star.png');
		}

		.Score--s5 & {
			width: 68px;
		}

		.Score--s4 & {
			width: 54px;
		}

		.Score--s3 & {
			width: 40px;
		}

		.Score--s2 & {
			width: 26px;
		}

		.Score--s1 & {
			width: 12px;
		}
	}
}



/* SYMBOL COUNT
------------------------------------------------------------------------------*/

.SymbolCount {
	color: #999;
	font-size: 11px;
	font-weight: normal;
	margin-left: 1em;
	
	&--error {
		color: #E45959;
	}
}



/* REPLY
------------------------------------------------------------------------------*/

$ReplyShift: 10%;
	
.Reply {
	background-repeat: no-repeat;
	padding-bottom: 8px;
	max-width: 100%;
	margin-top: 15px;
	
	&__container {
		display: inline-block;
		max-width: 100%;
	}

	&__attachments {
		line-height: 0;
		word-spacing: 15px;
		user-select: none;
	}

	&--client {
		padding-right: $ReplyShift;
		
		.ReplyText {
			background-color: #efefef;
			
			&:before {
				background-image: resolve('icons/feedback-tail.png');
				left: 5px;
			}
		}
	}

	&--employee {
		padding-left: $ReplyShift;
		text-align: right;
		
		.ReplyText {
			background-color: #e9f8f3;
			
			&:before {
				background-image: resolve('icons/feedback-tail2.png');
				right: 5px;
			}
		}
	}
}

.ReplyText {
	border-radius: 4px;
	box-sizing: border-box;
	display: inline-block;
	padding: 15px;
	position: relative;
	text-align: left;
	word-wrap: break-word;
	max-width: 100%;
	
	&:before {
		content: "";
		display: block;
		width: width('icons/feedback-tail.png');
		height: height('icons/feedback-tail.png');
		position: absolute;
		top: 100%;
	}

	.ReviewChat & {
		display: block;
	}
}

.ReplyControls {
	font-size: 11px;
	padding: 5px 25px 0 0;
	
	&__remove {
		margin-left: 10px;
	}
}

.ReplyInfo {
	font-size: 11px;
	margin-bottom: 5px;
	text-align: left;
	color: #666;
	
	&__author {
		color: #000;
		font-weight: bold;
	}

	&__date {
		display: inline-block;
	}
}



/* TABLE ITEMS
---------------------------------------------------------------------- */

/*
<div class="Items">
	<div class="Items__list">
		<span class="Items__header">
			<span class="Item__box"></span>
			<span class="Item__box"></span>
		</span>
		<a class="Item" href="#">
			<span class="Item__box">a</span>
			<span class="Item__box">a</span>
		</a>
	</div>
</div>
*/

.Group {
	box-sizing: border-box;
	display: table;
	width: 100%;
	
	&.ib {
		display: block;
	}
	
	&.fixed {
		table-layout: fixed;
	}
}
.Row {
	display: table-row;
}
.Box {
	display: table-cell;
	vertical-align: middle;
	
	&--shrink {
		width: 1px;
		white-space: nowrap;
	}
	
	&--stretch {
		width: 100%;
	}

	&--right {
		text-align: right;
	}
}
.Group.ib .Box {
	display: inline-block;
}


.Group--submit {
	.Box {
		&:last-child {
			text-align: right;
		}
	}
}

.Items {
	& + .pageControl {
		border-top: 1px solid $divider;
	}

	.BadgeStatus__indicator,
	.CommentTrigger {
		cursor: help;
	}
	
	&:before {
		@mixin Overlay rgba(255,255,255,.5) {
			content: "";
			transition: .2s;
			display: block;
			opacity: 0;
			visibility: hidden;
		};
	}
	&.Items--loading:before {
		opacity: 1;
		visibility: visible;
	}
}
.Items__list {
	@extend .Group;
	margin-top: 1em;
}
.Items__header {
	@extend .Row;
	color: #777;
	font-size: 11px;
}
.Item {
	@extend .Row;
	color: #000;
	line-height: 1.4;

	a&:hover,
	a&.hover {
		background-color: #e9f8f3;
		color: #000;
	}

	.Item__box {
		border-top: 1px solid #dadada;
	}

	&__counter {
		background-color: $accent;
		border-radius: .2em;
		color: #fff;
		padding: 0 .25em;
	}

	&__look {
		@mixin btnLook;

		display: block; 
		width: 42px;
		height: 32px;
	}

	&__subcolumn {
		display: inline-block;
		width: 50%;
		padding: 0 2px;
		box-sizing: border-box;
	}

	&--disable,
	&--disabled /*    */ {
		background-color: #efefef;
		color: #999;
	}

	&--joinedUp .Item__box {
		border-top: none;
	}
}

.Item__box {
	@extend .Box;
	padding: 10px;
	
	&:first-child {
		padding-left: $indentX;
	}
	&:last-child {
		padding-right: $indentX;
	}

	.Items__header & {
		vertical-align: bottom;
	}

	&--withCounter {
		white-space: nowrap;
	}
}
.Item__box--comment {
	padding-right: 5px;
	
	& + .Item__box--number {
		padding-left: 0;
	}
}

.Item__sort {
	display: inline-block;
	position: relative;
	
	&:after {
		content: "";
		border: solid transparent;
		border-width: 0 3px 0;
		display: none;
		vertical-align: middle;
		margin-left: 4px;
		margin-top: -.2em;
		position: absolute;
		right: 0;
		top: .7em;
	}

	&--asc,
	&--desc {
		& {
			padding-right: 10px;
		}

		&:after {
			display: inline-block;
		}
	}

	&--asc {
		&:after {
			border-top-width: 3px;
			border-top-color: currentColor;
		}
	}

	&--desc {
		&:after {
			border-bottom-width: 3px;
			border-bottom-color: currentColor;
		}
	}
}

.Item__enough {
	color: $main;
}
.Item__notEnough {
	color: $error;
}

.Item__appendix {
	color: #888;
	display: block;
}

.Item__remove {
	@mixin btnRemove;
	margin-left: .3em;
}

.Item__additional {
	color: #888;
	font-size: 11px;
	
	&--normal {
		color: inherit;
		font-size: inherit;
	}
}

.Items--sortable {
	border-y: 1px solid $divider;
	overflow-y: auto;

	.Items__list,
	.Item {
		display: block;
		width: 100%;
	}
	.Items__list {
		position: relative;
	}
	.Item {
		border-y: 1px solid $divider;
		cursor: move;
		
		&:first-child {
			border-top-width: 0;
		}
		&:last-child {
			border-bottom-width: 0;
		}

		&--placeholder {
			visibility: hidden;
		}
		&--sorting {
			background-color: #fff;
			box-shadow: 0 0px 7px rgba(0,0,0,.4);
		}
	}
	.Item + .Item {
		margin-top: -1px;
	}
	.Item__box {
		display: inline-block;
		border-top-width: 0;
	}

	.Item__box--drag {
		padding-left: 0;
	}
	.Item__drag {
		background-image: resolve('icons/organize_grey.png');
		width: width('icons/organize_grey.png');
		height: height('icons/organize_grey.png');
		display: inline-block;
	}
}



/* TroubleReport
---------------------------------------------------------------------- */

footer .TroubleReport {
	box-sizing: border-box;
	width: 100%;
	padding: 18px 20px 0px;
	text-align: center;
}



/* FancyModal
---------------------------------------------------------------------- */

/*
<div class="FancyModal">
	<h2 class="FancyModal__header"> </h2>
	<div class="FancyModal__content">
		kek!
	</div>
	<div class="FancyModal__control">
		<button class="btnMain FancyModal__submit"></button>
		<button class="btnMain btnOutline FancyModal__cancel"></button>
	</div>
*/

.FancyModal {
	padding-y: 25px;
}
.FancyModal__header,
.FancyModal__content,
.FancyModal__control {
	padding-x: $indentX;
}
.FancyModal__header {
	font-size: 22px;
	font-weight: normal;
	margin-top: 0;
	margin-bottom: .75em;
}

.FancyModal__content {
	&--accent {
		background-color: #efefef;
		padding-y: 15px;
		
		& + & {
			margin-top: 3px;
		}
	}
}

.FancyModal__control {
	background-color: #efefef;
	margin-top: 15px;
	margin-bottom: -25px;
	padding-y: 15px;
}
.FancyModal__submit {
	margin-right: .75em;
}

.Group--fancyModalHeader {
	margin-bottom: 1.5em;
	
	.Box {
		vertical-align: baseline;
		
		&--header {
			white-space: nowrap;
			width: 1px;
			
			.FancyModal__header {
				margin-bottom: 0;
			}
		}

		&--date {
			color: $info;
		}
	}
}

.FancyModal--alert {
	color: #555;
	padding: 50px 20px;
	text-align: center;
	width: 500px;
	
	.FancyModal__header {
		margin-bottom: 30px;
	}
}

/*    */
.FancyModal--trouble {
	width: 625px;
	
	.Form__set {
		width: auto;
	}
}

.FancyModalSuccess {
	padding: 15px 0;
	text-align: center;
}

/*
<div class="FancyModal FancyModal--message">
	<div class="FancyModal__header"> </div>
	 
	<div class="FancyModal__controls">
		<button class="btnMain btnOutline"></button>
	</div>
</div>
*/
.FancyModal--message {
	box-sizing: border-box;
	width: 480px;
	padding: 35px;
	text-align: center;
	font-size: 15px;

	.FancyModal__controls {
		margin-top: .75em;
	}
}



/* CONTROLPANEL
------------------------------------------------------------------------------*/

.controlPanel {
	background-color: #efefef;
	position: relative;

	& + &,
	& + .Items /*  -> Orders */ {
		margin-top: 3px;
	}
	
	&__header {
		margin-bottom: .5em;
	}

	&__item {
		display: inline-block;
		margin-right: 1em;
	}
}

.controlPanel--box {
	display: inline-block;
	
	& + & {
		margin-left: 1em;
	}
}



/* UPDATE PANEL
------------------------------------------------------------------------------*/

.updatePanel {
	background-color: #e9f8f3;
	color: #000;
	display: block;
	margin-y: 3px;
	padding: 12px;
	text-align: center;
	
	&:hover {
		background-color: #f1fdf9;
	}

	&__counter {
		font-weight: bold;
	}
}



/* FILTER
------------------------------------------------------------------------------*/

.filters {
	margin-bottom: 0;
	
	.mainContent &.contentWrapper {
		padding-top: 0;
	}
}
.filter {
	display: inline-block;
	margin-right: 20px;
	vertical-align: top;
	min-width: 75px;
	
	&,
	&:first-child {
		margin-top: 10px;
	}
}
.filter:last-child {
	margin-right: 0;
}
.filter--alignBottom {
	vertical-align: bottom;
}
.filter--alignBottom .btn {
	margin-top: 10px;
}
.filter--singleLine {
	vertical-align: bottom;
	margin-bottom: 6px;
}
.filter__submit {
	@mixin btnMain;
	@mixin btnOutlineMain;
}

.filter__button {
	@mixin btnMain;

	&--sub {
		@mixin btnOutlineMain;
	}

	& + & {
		margin-left: 5px;
	}
}


.filter__fieldDivider {
	&:after {
		content: "\2014";
		margin-x: .4em;
	}
}

.filter__header {
	display: block;
	font-size: 12px;
	font-weight: normal;
	margin-top: 0;
	margin-bottom: 7px;
}

.filter__field {
	width: 100%;
}

.filters--defined {
	.filter {
		min-width: 0;
		max-width: 200px;
	}
	.filter__header {
		color: #555;
		margin-bottom: 3px;
	}
	.filter__data {
		font-weight: bold;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.filter--alignBottom {
		margin-top: 12px;
		vertical-align: middle;
	}
}

.filter {
	&__indent {
		margin-left: -18px;
	}

	&--shift {
		padding-left: 18px;
	}
}

.filter__photo,
.filter__option {
	display: inline-block;
	vertical-align: middle;
}

.filter__photo {
	max-width: 42px;
	height: auto;
	margin-right: 5px;
}

.filter__href {
	font-size: 13px;
}

.filter__value {
	font-size: 11px;
	
	.filter--responsible & {
		color: #000;
		font-size: 13px;
		font-weight: bold;
	}
}

.filter--responsible {
	background-image: resolve('icons/no-face.png');
	background-repeat: no-repeat;
	background-position: 0 50%;
	padding-left: 40px;
}



/* BLANKPANEL
------------------------------------------------------------------------------*/

.mainContent .blankPanel {
	padding: $indentX;
	text-align: center;
}
.blankPanel .header {
	font-size: 24px;
	margin: 0;
}
.blankPanel .content {
	font-size: 14px;
	line-height: 1.4;
	margin-top: 5px;
}



/* STATUSPANEL
------------------------------------------------------------------------------*/

.statusPanel {
	background-color: #f7f7f7;
	border: 1px solid #aaa;
	color: #777;
	display: block;
	padding: 7px 10px;
	text-align: center;
}
.statusPanel.error {
	background-color: #fee;
	border-color: #e09292;
	color: #bc1616;
}



/* Chart
------------------------------------------------------------------------------*/

.Chart {
	margin-top: 15px;
	
	.mainContent &__header {
		font-size: 14px;
		font-weight: normal;
		margin-top: 0;
		margin-bottom: 1em;
	}

	&Info {
		margin-bottom: 1.5em;
		
		&__item {
			margin-top: .25em;
		}	
	}

	&Legend {
		color: $info;
		display: block;
		font-size: 11px;
		margin: 0 0 2em;
		padding: 0 0 20px;
		border-bottom: 1px solid $divider;
		
		&Item {
			display: inline-block;
			margin-top: 0;
			margin-bottom: .5em;
			width: 33.33%;
			
			&__icon {
				display: inline-block;
				height: 8px;
				width: 8px;
				margin-right: .4em;
			}
		}
	}
}

.Chart--stat-search {
	.ChartLegendItem--1 {
		.ChartLegendItem__icon {
			background-color: #f39797;
		}
	}
	.ChartLegendItem--2 {
		.ChartLegendItem__icon {
			background-color: #ebb57f;
		}
	}
	.ChartLegendItem--3 {
		.ChartLegendItem__icon {
			background-color: #f2e38f;
		}
	}
	.ChartLegendItem--4 {
		.ChartLegendItem__icon {
			background-color: #d5d3ca;
		}
	}
	.ChartLegendItem--5 {
		.ChartLegendItem__icon {
			background-color: #9dc2e4;
		}
	}
	.ChartLegendItem--6 {
		.ChartLegendItem__icon {
			background-color: #99dbbd;
		}
	}
}

/*    */
.Chart--stat-search {
	.c3-line,
	.c3-axis-x .tick line {
		display: none;
	}
	.c3-bar {
		opacity: .5 !important;
		fill-opacity: 1 !important;
	}
	.c3-areas path,
	.c3-tooltip__color {
		opacity: .5 !important;
	}
	.c3-axis .tick line,
	.c3-axis .domain {
		stroke: #9f9f9f;
	}
	.c3-axis .domain {
		transform: translateX(.5px);
	}
	.c3-ygrid-line line,
	.c3-ygrid {
		stroke: #000;
		opacity: .075 !important;
	}
	.c3-xgrid,
	.c3-ygrid {
		transform: translateX(-1px);
		stroke-dasharray: 0;
	}
	.c3-xgrid {
		stroke: #000;
		opacity: .11 !important;
	}
}



/* IntIndicator -  
------------------------------------------------------------------------------*/

.IntIndicator {
	background-color: grey;
	border-radius: 50%;
	display: inline-block;
	height: 10px;
	width: 10px;
	
	&--noAuth { background-color: #d1d1d1; }
	&--moreDays { background-color: $accent; }
	&--noOrder { background-color: #f3bc13; }
	&--withOrder { background-color: $main; }

	&[data-content] {
		cursor: help;
		position: relative;
		
		&:before {
			content: "";
			position: absolute;
			top: -7px;
			right: -7px;
			bottom: -7px;
			left: -7px;
		}

		&:hover {
			opacity: .75;
		}
	}
}



/* ControlGroup -     
------------------------------------------------------------------------------*/

.ControlGroup {
	display: inline-block;
	white-space: nowrap;
	
	&__item {
		&:not(:last-child) {
			&,
			.Bubble__trigger {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
			}
		}

		& + & {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			border-left: 0;
		}
	}
}



/* LAYOUT
------------------------------------------------------------------------------*/

body {
	background-color: #d7dbdc;
	font-size: 1.2em;
}



/* HEADER
------------------------------------------------------------------------------*/

header {
	background-color: #2d2d2d;
	font-family: Arial,Verdana,serif;
	font-size: 12px;
	line-height: 23px;
	padding: 0 15px 0 10px;
	margin-bottom: 25px;
}

header > ul {
	margin: 0;
	padding: 0;
}

header .sites {
	float: left;
}

header .user {
	float: right;
}

header li {
	display: inline-block;
	vertical-align: top;
	margin: 0;
}
header .active {
	cursor: default;
}

header li a,
header li span {
	color: #bbb;
	display: inline-block;
	padding: 5px 10px 6px;
	text-decoration: none;
	vertical-align: top;
	line-height: 23px;
}

header .active span {
	color: #fff;
}

header li a:hover {
	color: #fff;
}

header .user a,
header .user span {
	padding-right: 4px;
	padding-left: 4px;
}

header > ul > li > a:hover {
	background: #4B4B4B;
	cursor: pointer;
	color: #fff;
}



/* FOOTER
------------------------------------------------------------------------------*/

footer {
	margin: 0 auto 25px;
	width: 940px;
}
footer .copy {
	color: #666;
	font-size: 11px;
	margin-bottom: 0;
	text-align: center;
}



/* NAV
------------------------------------------------------------------------------*/

$navMain: #3b454a !default; 
$navSub: #5e696f !default;

nav {
	position: relative;
	user-select: none;
}
nav li,
nav a {
	display: inline-block;
	vertical-align: baseline;
}
nav a {
	text-decoration: none;
}

nav .main,
nav .sub {
	margin-bottom: 0;
	padding-left: 0;
	
	& > li {
		margin-top: 0;
	}
}

nav .main {
	background-color: $navMain;
}
nav .main a {
	color: rgba(255,255,255,.75);
	font-size: 14px;
	font-weight: bold;
	padding: 10px 18px;
	position: relative;
}
nav .main a:hover {
	color: #fff;
}
nav .main .active a {
	background-color: $navSub;
	color: #fff;
}

nav .main .active a:hover {
	color: rgba(255,255,255,.75);
}

nav .sub {
	background-color: $navSub;
	padding: 5px $indentX;
}
nav .sub li {
	margin: 6px 5px 6px 0;
}
nav .sub li:first-child {
	margin-left: -8px;
}
nav .sub a {
	color: rgba(255,255,255,.75);
	padding: 3px 8px;
	transition: all .3s ease;
}
nav .sub a:hover {
	background-color: rgba(255,255,255,.1);
	color: #fff;
	transition: all .1s ease;
}
nav .sub .active a {
	background-color: #fff;
	border-radius: 2px;
	color: #363636;
	transition: all .2s ease;
}
nav .sub .active a:hover {
	color: rgba(0,0,0,.65);
}



/* MAIN
------------------------------------------------------------------------------*/

.pageMain {
	margin: 0 auto 25px;
	width: 940px;
}
.pageMain .mainHeader {
	color: #666;
	font-size: 24px;
	margin-bottom: 25px;
}

.mainContent {
	position: relative;
	background-color: #fff;
	border: 1px solid #cacaca;
	border-top-width: 0;
	z-index: 5;
}

.mainContent h2 {
	font-size: 22px;
	font-weight: normal;
	margin-bottom: 25px;
	
	&:last-child {
		margin-bottom: 0;
	}
}

.mainContent h3 {
	font-size: 18px;
	font-weight: normal;
	margin-bottom: 18px;
}

.ContentWrapper,
.mainContent .contentWrapper {
	padding: 10px $indentX;
	
	&__header {
		font-size: 14px;
		font-weight: bold;
		margin-top: .5em;
		margin-bottom: .3em;
	}
}
.contentWrapper.blankPanel {
	padding-top: 50px;
	padding-bottom: 50px;
 }
.contentWrapper.description {
	background-color: #f6f5eb;
}
.contentWrapper.error {
	background-color: #f8e9e9;
}
.contentWrapper.success {
	background-color: #eff5da;
}
.contentWrapper.tip {
	background-color: #fbfbd8;
}
.contentWrapper.accent {
	background-color: #efefef;
}

.contentWrapper.disabled {
	filter: grayscale(100%);
	position: relative;
	
	&:after {
		content: "";
		background-color: rgba(239,239,239,.6);
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
}


.Header {
	&.contentWrapper {
		padding-top: 15px;
		padding-bottom: 15px;
	}

	.mainContent &__title,
	&__box--header &__title {
		line-height: 1.25;
	}

	.mainContent &__title {
		margin-top: 0;
		margin-bottom: 0;
	}

	&__description {
		margin-top: 1em;
	}
}
.Header__group {
	@extend .Group;
}
.Header__box {
	@extend .Box;
	
	& + & {
		padding-left: $indentX;
	}
	
	&--thin,
	&--back {
		width: 1px;
		white-space: nowrap;
	}

	&--right {
		text-align: right;
	}
}
.Header__back {
	@extend .btnBack;
	@mixin btnOutlineMain;
}



/* UNAUTHORIZED
------------------------------------------------------------------------------*/

.unauthorized {
	display: block;
	min-width: 0;
}

.unauthorized .pageMain {
	background-color: #fff;
	border: 1px solid #a7adb2;
	box-shadow: 0 1px 1px rgba(0,0,0,.1);
	font-size: 14px;
	margin-top: 13%;
	padding: 35px 45px;
	text-align: center;
	width: 350px;
}
.unauthorized .pageMain .mainHeader {
	display: none;
}
.unauthorized .pageMain .mainContent {
	border: none;
	box-shadow: none;
}
.unauthorized .pageMain h2 {
	color: #000;
	font-size: 20px;
	margin-bottom: 25px;
	text-shadow: none;
}
.unauthorized .statusPanel {
	margin-top: -10px;
	margin-bottom: 20px;
}
.unauthorized .pageMain input {
	display: block;
	margin-bottom: 15px;
	padding: 8px 6px;
	width: 100%;
}
.unauthorized .pageMain .btn {
	font-weight: bold;
	margin-bottom: 0;
	padding: 7px 45px;
	width: auto;
}

.unauthorized .statusPanel {
	margin-top: -10px;
	margin-bottom: 20px;
}



/* PROGRESS
------------------------------------------------------------------------------*/

/*
<div class="Progress">
	<div class="Progress__bar">
		<div class="Progress__done" style="width: 37%;"><span class="Progress__title">37%</span></div>
	</div>
</div>
*/

.Progress {
	font-size: 10px;

	&__bar {
		height: 17px;
		border-radius: 1em;
		background-color: #e7e7e7;
	}

	&__done {
		min-width: 3.5em;
		height: 100%;
		border-radius: 1em;
		text-align: center;
		background-color: #28bd8b;
	}

	&__title {
		color: #fff;
		line-height: 17px;
	}
}



/* ITEMS BY CODE
------------------------------------------------------------------------------*/

.Items--byCode {
	.Items__header .Item__box {
		padding-bottom: 0;
	}

	.Item + .Item .Item__box {
		padding-top: 0;
	}

	.Item__box {
		border-top-width: 0;
		padding-right: 0;
		
		&:first-child {
			padding-left: 0;
		}
		&:last-child {
			padding-right: 0;
		}
		
		input {
			text-align: center;
		}

		&--code,
		&--count {
			white-space: nowrap;
			width: 1px;
		}

		&--code {
			input {
				width: 60px;
			}
		}

		&--count {
			input {
				width: 50px;
			}
		}

		&--drag {
			padding-left: 0;
		}
	}

	.Item__drag {
		background-image: resolve('icons/organize_grey.png');
		width: width('icons/organize_grey.png');
		height: height('icons/organize_grey.png');
		display: inline-block;
	}
}

.Form__field .Items--byCode {
	.Item__box--price,
	.Item__box--reason {
		input {
			width: 100%;
		}
	}

	.Item__box--price {
		width: 70px;
	}
}

.Form__field {
	.Print__field--invoice {
		margin-right: 1em;
		
		.Form__input.short {
			width: 100px;
		}
	}
}

